@use "global" as *;

.top-blog-card__title,
.top-blog-card__text,
.top-blog-card__date {
  color: $black2;

}

.top-blog-card__title {
  font-weight: $medium;
  font-size: rem(18);
  line-height: 2;
  letter-spacing: 0.05em;
}

.top-blog-card__text,
.top-blog-card__category,
.top-blog-card__date {
  font-weight: $regular;
  font-size: rem(14);
  line-height: 1.85714;
  letter-spacing: 0.06em;
}


.top-blog-card__info {
  padding: 0 rem(24);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.top-blog-card__category {
  color: $black;
  background-color: #FFEA2E;
  padding: rem(6) rem(5);
  line-height: 1;
  position: absolute;
  bottom: rem(15);
  left: rem(24);
}

.top-blog-card__date {
  position: absolute;
  bottom: rem(15);
  right: rem(24);
}

.top-blog-card__body {
  padding: rem(4) rem(24) rem(49);
}

.top-blog-card__body:hover {
  background-color:  $black2;
}

.top-blog-card:hover :not(.top-blog-card__category):not(.top-blog__update):not(.top-blog__update>p) {
  background-color:  $black2;
  transition: .3s;
  color: #fff;
}

.top-blog-card__text {
  padding-top: rem(4);
}

.top-blog-card {
  position: relative;
  display: block;
  background-color: #fff;
    max-width: rem(600);
  margin: 0 auto;
}

.top-blog-card__img {
  aspect-ratio: 350 / 233;
  // padding-top: 66.571429%; 
  //233/350*100%
  // position: relative;
}

.top-blog-card__img img {
  // position: absolute;
  // top: 50%;
  // left: 50%;
  // transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
  // object-position: center;
}
